// libraries
import React, {useContext} from 'react';
import {
  Link
} from "react-router-dom";

// react hooks
import { GlobalContext } from './../../state-manager/globalState';


// components
import CountryCard from './countryCard';

function Predictions() {

  const { countries } = useContext( GlobalContext );
  const { data , has_error } = countries;


  if ( has_error )
    return (
      <div className="countries">
        <h1 className="title" > Infected countries </h1>
        <div className="error">Oops , something went wrong while loading countries !</div>
      </div>
    )

  if ( data.length === 0 )
    return (
      <div className="countries">
        <h1 className="title" > Infected countries </h1>
        <div className="error">Oops , we still have no data to display</div>
      </div>
    )
  
  return (
    
    <div className="countries">
    	<h1 className="title" > Infected countries </h1>
    	<div className="container">
    		{
    			data.map( country => (
    				<CountryCard key={ country.id } {...country} />
    			))
    		}
    	</div>
    </div>

  );
}

export default Predictions;