import React, {Component, createElement} from 'react';

import './image-styles.css';

class Image extends Component{

	state = {
		loaded : false,
		error: false
	}
	
	handleLoad(){
		this.setState({ loaded: true })
	}
	handleError(){
		this.setState({  error: true })
		console.log("ERROR")
	}

	componentDidMount(){
	}

	Loading( loaded ){
		return loaded ? 
			null:
			<small className="anim-container" >Loading ...</small>
	}

	render(){
		const { error,  loaded } = this.state;
		if ( error ){
			return null
		}
		return(
			<div style={{ height: '100%', width: '100%' }} >
				{ this.Loading( loaded ) }
				<img style={{ opacity: loaded ? 1:0 }} src={this.props.src} onLoad={ () => this.handleLoad() }  onError={ () => this.handleError() } />
				
			</div>
		)

	}

}
export default Image

