// libraries
import React, { useState, useEffect, useRef, useContext } from 'react';
import Chart from 'chart.js';
import {
  Link, useParams, Redirect
} from "react-router-dom";

import { GlobalContext } from './../../state-manager/globalState';


import {formatNumberToMonth, getTable, calcPercent} from './../../utils/utils';
 
function CountryPredictions() {

	// Canvas elements for drawing
 	const ctxLine = useRef(null);
 	const ctxPie = useRef(null);

 	// react hooks 
	const { getCountry, predictions, setPredictions, fetchPredictions  } = useContext( GlobalContext );
 	const [ isLoading, setIsLoading ] = useState(true);
 	const [ country, setCountry ] = useState(null);
 	const [ table, setTable ] = useState([
 		{ month: 'Mars',  deaths:0, cases:0, recovered: 0  },
 		{ month: 'April', deaths:0, cases:0, recovered: 0  },
 		{ month: 'May', deaths:0, cases:0, recovered: 0  },
 		{ month: 'June', deaths:0, cases:0, recovered: 0  },
 	]);


	const { id } = useParams();
 	

	useEffect( () => {

 		// get  The necessary data
		setCountry(getCountry(Number(id)));
 		fetchPredictions(id)
		.then( ( predictions ) =>{
			setPredictions(predictions);
			setIsLoading(false);
			setTable( getTable(predictions) );
		})
		.catch( ( { err, predictions} ) =>{
			console.log(predictions);
			console.log(err);
			setPredictions(predictions);
			setIsLoading(false);
		});


 	}, []);


 	useEffect( () => {
 		// start drawing after fetching data
 		if ( ! isLoading && ! predictions.has_error ){
	 		const primaryColor = "rgba(83, 109, 254, .6)";
	 		const secondaryColor = "tomato";
	 		const greenColor = "#61D295";


	 		// Draw Line
	 		let scatterDataSet;
	 		if ( predictions.deaths[2].y > 0 ){
		 		scatterDataSet = [
		 			 {
					    label: 'Infections',
					    borderColor: primaryColor ,
					    color: primaryColor,
			            pointBorderColor: primaryColor,
			            pointBackgroundColor: primaryColor ,
			            fill: true,
			            showLine: true,
						backgroundColor: primaryColor,
					    data: predictions.cases,
					  },{
					    label: 'Deaths',
					    borderColor: secondaryColor,
			            pointBorderColor: secondaryColor,
			            pointBackgroundColor: secondaryColor,
			            fill: true,
			            showLine: true,

						backgroundColor: secondaryColor,
					    data: predictions.deaths,
					}
		 		];
		 	}else{
		 		scatterDataSet = [
		 			 {
					    label: 'Infections',
					    borderColor: primaryColor ,
					    color: primaryColor,
			            pointBorderColor: primaryColor,
			            pointBackgroundColor: primaryColor ,
			            fill: true,
			            showLine: true,
						backgroundColor: primaryColor,
					    data: predictions.cases,
					  }
		 		];
		 	}
			const scatterChart = new Chart(ctxLine.current,{
					type: 'scatter',
					data: {
					  datasets: scatterDataSet,
					},
					options: {
						elements: {
		                    point:{
		                        radius: 1,
		                        // borderDash: [3,],
		                    }
		                },
						legend: {
			            	labels: {
				                // This more specific font property overrides the global property
				                fontColor: 'black',
				                fontSize: 14,
			            	}
			        	},
					  responsive: true,
					  scales: {
			            xAxes: [{
			                gridLines: {
			                    zeroLineColor: "transparent"
							},
			                ticks: {
			                	autoSkip: false,
			                	stepSize: 1,
			        			min:0,
			        			max: 92,
			                    padding: 20,
			                    fontSize: 14,
			                    fontColor: "rgba(0,0,0,0.5)",
			                    fontStyle: "bold",
			                    callback: ( value ) => formatNumberToMonth(value)
			                }
			            }],
			            yAxes: [{
			                ticks: {
			                    fontColor: "rgba(0,0,0,0.5)",
			                    fontStyle: "bold",
			                    beginAtZero: true,
			                    // maxTicksLimit: 5,
			                    padding: 20,
			                    callback: ( value ) => value.toLocaleString(),

			                },
			                gridLines: {
			                    padding: 20,
			                    zeroLineColor: "transparent"

			                }
						}],
					  }
					}
			   }
			)

			
			const { deaths, cases, recovered } = country;
			// Draw Pie
			const myPieChart = new Chart(ctxPie.current, {
			    type: 'doughnut',
			    data:{
				    datasets: [{
				        data: [ deaths, cases,  recovered ],
				        fill: true,
	            		backgroundColor: [
				            secondaryColor,
				            primaryColor,
				            greenColor
			            ],
			            
					 }],

				    // These labels appear in the legend and in the tooltips when hovering different arcs
				    labels: [
				        'Deaths',
				        'Cases',
				        'Recovered'
				    ]
				},
				options: {
					legend: {
		            	labels: {
			                // This more specific font property overrides the global property
			                fontColor: 'black',
			                fontSize: 14,
		            	}
		        	},
					cutoutPercentage: 50
				}
			});
		}

 	}, [isLoading]); 





 	if ( isLoading )

 		return <div className="predictions"> <div className="loading">Loading ...</div> </div>

 	if ( country && !isLoading ){
 		// necessary data for rendering
 		const { name, deaths, recovered, cases, population  } = country;
 		const { has_error } = predictions;
 		if ( predictions.has_error )
 			return (
 			   <div className="predictions">
					<div className="top-bar">
						<Link to="/predictions" className="btn-back icon-only" ><i className="fa fa-arrow-left"/> </Link>
						<h1 className="title" >Predictions of the number of infected cases in {name} ( Mars - June )</h1>
					</div>
		        <h1 className="title" > Oops </h1>
		        <div className="error">something went wrong while loading countries !</div>
		      </div>
 			)

		return (

			<div className="predictions">
				<div className="top-bar">
					<Link to="/predictions" className="btn-back icon-only" ><i className="fa fa-arrow-left"/> </Link>
					<h1 className="title" >Predictions of the number of infected cases in {name} ( Mars - June )</h1>
				</div>
				<div className="details">
					<div className="chart-infos">
						<div className="chart">
							<canvas width="120%" height="80%"  ref={ ctxLine } />
						</div>
						<div className="infos">

			    		<h3>Total:  { cases + deaths + recovered } </h3>

			    		<canvas width="120%" height="100%"  ref={ ctxPie } />
							
						</div>
					</div>
					<div className="table-4">

						<div className="line">
							<span> <small className="bold-md" >Month</small> </span>
							<span> <small className="bold-md" >Infections</small> </span>
							<span> <small className="bold-md" >Deaths</small> </span>
							<span> <small className="bold-md" >Recovered</small> </span>
							
						</div>
						{
							table.map( ({month, deaths, recovered, cases}, i) =>(
								<div key={i} className="line">
									<span> <small className="bold-md" >{month}</small> </span>
									<span> <small>{cases >= 0 ? cases.toLocaleString():0}</small> </span>
									<span> <small>{deaths >= 0 ? deaths.toLocaleString():0}</small> </span>
									<span> <small>{recovered >= 0 ? recovered.toLocaleString():0}</small> </span>

								</div>
							))
						}
						
							
					</div>
				</div>	

			</div>
		);
	}
	return (
		<Redirect to="/" />
	)

}

export default CountryPredictions;