
export function formatNumber(number){

	if ( number < 1000 )
		return number
	// 1.5M format
	if ( number >= 1000000){
		const beforeComma = Math.trunc( number / 1000000 );
		const afterComma  = Math.trunc((number % 1000000) / 1000);
		
		return afterComma === 0 ? `${beforeComma}M`:`${beforeComma},${afterComma}M`;
	}
	// 1.5K format
	if ( number >= 1000 ){
		const beforeComma = Math.trunc( number / 1000 );
		const afterComma  = Math.trunc((number % 1000) / 100);

		return afterComma === 0 ? `${beforeComma}K`:`${beforeComma},${afterComma}K`;
	}

}


export function formatNumberToMonth(value){
	switch  ( value ){
		case 3: return 'Mars';
		case 27: return 'April';
		case 58: return  'May';
		case 88: return 'June';
		default: return null
	}
}

export function getTable(predictions){
	const table = [];

	const { deaths, cases, recovered } = predictions;
	for (let i=0; i < deaths.length; i++){

		const month = formatNumberToMonth(i + 1);
		if ( month  ){
			table.push({
				month: month, deaths: deaths[i].y , 
				cases: cases[i].y, 
				recovered: recovered[i].y
			})	
		}
	}

	return table;
}

export function calcPercent( total, value ){

	const calc = Math.trunc( (value / total) * 100 );

	return 	calc
}